import { yupVOValidation } from 'src/yup/utils';
import * as yup from 'yup';

import { VOHost, VOTCPPort } from '@core/value_objects';
import {
  AUTH_EXPIRATION_MAX_DAYS,
  AUTH_EXPIRATION_MIN_DAYS,
} from 'src/domain/settings/consts';

export const yupValidationSchema = yup
  .object({
    smtp_host: yup
      .string()
      .required('Ingrese el host SMTP')
      .test('valid-host', 'Ingrese un host SMTP válido', (v) =>
        yupVOValidation(VOHost, v)
      ),
    smtp_username: yup.string().required('Ingrese el usuario SMTP'),
    smtp_password: yup.string().required('Ingrese la contraseña SMTP'),
    smtp_port: yup
      .number()
      .required('Ingrese el puerto SMTP')
      .typeError('Ingrese un número de puerto SMTP válido')
      .test('is-port', 'Ingrese un puerto SMTP válido', (v) =>
        yupVOValidation(VOTCPPort, v)
      ),
    smtp_use_tls: yup.boolean().required(),
    auth_expiration_days: yup
      .number()
      .min(
        AUTH_EXPIRATION_MIN_DAYS,
        `El tiempo de espera debe ser mayor a ${AUTH_EXPIRATION_MIN_DAYS}`
      )
      .max(
        AUTH_EXPIRATION_MAX_DAYS,
        `El tiempo de espera debe ser menor a ${AUTH_EXPIRATION_MAX_DAYS}`
      )
      .required('Ingrese el tiempo de espera para logout')
      .typeError('Ingrese el tiempo de espera para logout'),
    admin_email: yup
      .string()
      .required('Ingrese el correo electrónico del Administrador')
      .email('Ingrese un correo electrónico válido'),
    user_warning_message_title: yup
      .string()
      .required('Ingrese el título del mensaje de advertencia'),
    user_warning_message_content: yup
      .string()
      .required('Ingrese el contenido del mensaje de advertencia'),
    user_disabled_message_title: yup
      .string()
      .required('Ingrese el título del mensaje de inhabilitación'),
    user_disabled_message_content: yup
      .string()
      .required('Ingrese el contenido del mensaje de inhabilitación'),
  })
  .required();
