import { isHost } from '../validation/base';
import { type VOInput, ValueObject } from './base';

export class VOHost extends ValueObject<string> {

  validate(input: VOInput<string>): string {
    let value = input;

    if (typeof value === 'string') {
      value = value.trim().toLowerCase();
      if (isHost(value)) return value;
    }
    
    this.throwError(input);
  }

}
